'use client';

import { useEffect, useState } from 'react';
import { Users, UserCheck, TrendingUp, AlertCircle, Calendar, GraduationCap, Coins } from 'lucide-react';
import { motion } from 'framer-motion';
import { supabase } from '@/lib/supabase';

export default function DashboardPage() {
    const [stats, setStats] = useState({
        students_count: 0,
        staff_count: 0,
        attendance_today: 0,
        fees_collected: 0
    });
    const [recentLogs, setRecentLogs] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetchDashboardData();
    }, []);

    const fetchDashboardData = async () => {
        try {
            // Parallel Fetching for speed
            const [students, staff, logs] = await Promise.all([
                supabase.from('students').select('*', { count: 'exact', head: true }),
                supabase.from('staff').select('*', { count: 'exact', head: true }),
                supabase.from('logs').select('*').order('created_at', { ascending: false }).limit(5)
            ]);

            // Fake attendance/fees for demo until data is real
            setStats({
                students_count: students.count || 0,
                staff_count: staff.count || 0,
                attendance_today: 0, // Need accurate attendance table query
                fees_collected: 0
            });

            setRecentLogs(logs.data || []);
        } catch (error) {
            console.error('Error fetching dashboard data:', error);
        } finally {
            setLoading(false);
        }
    };

    const cards = [
        {
            title: 'إجمالي الطلاب',
            value: stats.students_count,
            icon: GraduationCap,
            trend: '+12 هذا الأسبوع',
            color: 'text-blue-600',
            bg: 'bg-blue-50'
        },
        {
            title: 'الحضور اليوم',
            value: `${stats.attendance_today}%`,
            icon: UserCheck,
            trend: 'معدل طبيعي',
            color: 'text-emerald-600',
            bg: 'bg-emerald-50'
        },
        {
            title: 'هيئة التدريس',
            value: stats.staff_count,
            icon: Users,
            check: 'جميعهم متواجدون',
            color: 'text-indigo-600',
            bg: 'bg-indigo-50'
        },
        {
            title: 'المتحصلات (شهر)',
            value: '0 ج.م',
            icon: Coins,
            trend: 'تم تحصيل 0%',
            color: 'text-amber-600',
            bg: 'bg-amber-50'
        },
    ];

    if (loading) {
        return (
            <div className="flex-center" style={{ height: '50vh' }}>
                <div style={{ width: '40px', height: '40px', border: '3px solid #e2e8f0', borderTopColor: '#0f172a', borderRadius: '50%', animation: 'spin 1s linear infinite' }} />
                <style jsx>{`@keyframes spin { 100% { transform: rotate(360deg); } }`}</style>
            </div>
        );
    }

    return (
        <div style={{ maxWidth: '1400px', margin: '0 auto' }}>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: '2rem' }}>
                <div>
                    <h1 style={{ fontSize: '1.5rem', fontWeight: 'bold' }}>نظرة عامة</h1>
                    <p style={{ color: 'var(--text-muted)' }}>مرحباً بك في لوحة تحكم المدرسة الرقمية.</p>
                </div>
                <div style={{ display: 'flex', gap: '0.75rem' }}>
                    <button className="btn btn-outline">
                        <Calendar size={18} />
                        <span>تقارير اليوم</span>
                    </button>
                    <button className="btn btn-primary" style={{ background: 'var(--primary)' }}>
                        <TrendingUp size={18} />
                        <span>تحديث البيانات</span>
                    </button>
                </div>
            </div>

            {/* Stats Cards */}
            <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))', gap: '1.5rem', marginBottom: '2rem' }}>
                {cards.map((card, idx) => (
                    <motion.div
                        key={idx}
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: idx * 0.1 }}
                        className="card"
                        style={{ display: 'flex', flexDirection: 'column', gap: '1rem', borderTop: `4px solid currentColor`, color: card.color.includes('blue') ? '#2563eb' : card.color.includes('emerald') ? '#10b981' : card.color.includes('amber') ? '#d97706' : '#4f46e5' }}
                    >
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                            <div>
                                <p style={{ fontSize: '0.9rem', color: 'var(--text-secondary)', fontWeight: '600' }}>{card.title}</p>
                                <h3 style={{ fontSize: '1.75rem', fontWeight: '800', color: 'var(--text-main)', marginTop: '0.25rem' }}>{card.value}</h3>
                            </div>
                            <div style={{ padding: '0.75rem', borderRadius: '0.75rem', background: card.bg.replace('bg-', 'var(--').replace('-50', '-50)') /* minimal hack for now, better to use vars */, backgroundColor: '#f1f5f9' }}>
                                <card.icon size={24} />
                            </div>
                        </div>
                        <div style={{ fontSize: '0.85rem', display: 'flex', alignItems: 'center', gap: '0.5rem', color: 'var(--text-muted)' }}>
                            <TrendingUp size={14} />
                            <span>{card.trend || card.check}</span>
                        </div>
                    </motion.div>
                ))}
            </div>

            <div style={{ display: 'grid', gridTemplateColumns: 'repeat(12, 1fr)', gap: '1.5rem' }}>
                {/* Recent Activity Section */}
                <div className="card" style={{ gridColumn: 'span 8' }}>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: '1.5rem' }}>
                        <h3 style={{ fontWeight: '700' }}>سجل النشاطات الحديثة</h3>
                        <button style={{ color: 'var(--brand)', fontSize: '0.9rem', fontWeight: '600' }}>عرض الكل</button>
                    </div>

                    <div className="table-wrapper">
                        <table>
                            <thead>
                                <tr>
                                    <th>النشاط</th>
                                    <th>المستخدم</th>
                                    <th>التوقيت</th>
                                    <th>الحالة</th>
                                </tr>
                            </thead>
                            <tbody>
                                {recentLogs.length === 0 ? (
                                    <tr>
                                        <td colSpan={4} style={{ textAlign: 'center', padding: '2rem', color: 'var(--text-muted)' }}>لا توجد بيانات حالياً</td>
                                    </tr>
                                ) : (
                                    recentLogs.map((log) => (
                                        <tr key={log.id}>
                                            <td style={{ fontWeight: '600' }}>{log.action}</td>
                                            <td>{log.user_id || 'System'}</td>
                                            <td style={{ direction: 'ltr', textAlign: 'right' }}>
                                                {new Date(log.created_at).toLocaleTimeString('ar-EG', { hour: '2-digit', minute: '2-digit' })}
                                            </td>
                                            <td>
                                                <span className="status-badge status-info">مكتمل</span>
                                            </td>
                                        </tr>
                                    ))
                                )}
                            </tbody>
                        </table>
                    </div>
                </div>

                {/* Quick Actions / Alerts */}
                <div className="card" style={{ gridColumn: 'span 4', display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
                    <div>
                        <h3 style={{ fontWeight: '700', marginBottom: '1rem' }}>تنبيهات هامة</h3>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}>
                            <div style={{ padding: '1rem', background: '#fff7ed', borderRight: '4px solid #f97316', borderRadius: '4px', fontSize: '0.9rem' }}>
                                <div style={{ fontWeight: 'bold', color: '#9a3412', marginBottom: '0.25rem' }}>سداد المصروفات</div>
                                <p style={{ color: '#c2410c' }}>موعد استحقاق القسط الأول يقترب.</p>
                            </div>
                            <div style={{ padding: '1rem', background: '#ecfdf5', borderRight: '4px solid #10b981', borderRadius: '4px', fontSize: '0.9rem' }}>
                                <div style={{ fontWeight: 'bold', color: '#065f46', marginBottom: '0.25rem' }}> اكتمال البيانات</div>
                                <p style={{ color: '#047857' }}>يرجى مراجعة ملفات الطلاب الجدد.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
